.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Темная полупрозрачная маска */
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-window {
    background-color: white;
    width: 300px;
    padding: 20px;
    border-radius: 8px;
    position: relative;
}

.modal-close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 18px;
    cursor: pointer;
}

.modal-content {
    margin-top: 20px;
}
